#portfolio {
    background-color: $bright-color;
    height: auto;
    min-height: 100vh;
    margin: 0 auto;
    padding: 0;
    a {
        color: black;
        text-decoration: none;
    }
}
.galeria-box {
    display: block;
    position: relative;
    margin: 0 auto;
    padding: 0;
    .galeria-box-caption {
        display: block;
        bottom: 0;
        min-height: 100%;
        text-align: center;
        -webkit-transition: all 0.35s;
        -moz-transition: all 0.35s;
        transition: all 0.35s;
        .image_box {
            margin: 0 auto;
            padding: 0;
            height: 300px;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            top: 50%;
            text-align: center;
            background-repeat: no-repeat;
            background-position: center;
            background-size: auto;
            position: relative;
        }
        .image_box--portfolio {
            background-image: url("../images/responsive.png");
        }
        .image_box--bhagaskara {
            background-image: url("../images/code.png");
        }
        .image_box--mailing {
            background-image: url("../images/design.png");
        }
        .image_box--sitonchair {
            background-image: url("../images/responsive.png");
        }
    }
}
.project-name {
    font-family: 'helvetica', sans-serif;
    text-transform: uppercase;
    font-size: $font_size_xl;
    font-weight: 200;
    color: $main-grey;
    text-align: center;
    position: relative;
}
@media(min-width:1100px) {
    .project-name {
        // font-family: $main_font;
        font-family: 'helvetica', sans-serif;
        text-transform: uppercase;
        font-size: $font_size_xl;
        font-weight: 200;
        padding-top: 10%;
        color: $main-grey;
        text-align: center;
        position: relative;
        bottom: -180px;
    }
}
@media(max-width:1100px) {
    .project-name {
        padding-top: 15%;
        bottom: -200px;
    }
}
@media(max-width:320px) {
    .galeria-box,
    .image_box {
        margin-right: 100px;
        margin-left: -80px;
    }
    .project-name {
        padding-top: 15%;
    }
}
.gallery {
    margin: 0 auto;
    padding: 0;
    position: relative;
    overflow: hidden;
    ul {
        li {
            text-align: center;
            position: relative;
            list-style: none;
            a {
                position: relative;
                margin: 0;
                padding: 0;
                line-height: 0;
            }
            a:before {
                position: absolute;
                width: 100%;
                height: 100%;
                top: 40%;
                left: 50%;
                margin: -14px 0 0 -16px;
                background: url("") no-repeat;
                content: "";
                opacity: 0;
                z-index: 1;
                -webkit-transition: all 0.3s linear;
                -moz-transition: all 0.3s linear;
                transition: all 0.3s linear;
            }
            a:hover:before {
                top: 50%;
                opacity: 1;
            }
            a:after {
                position: absolute;
                width: 100%;
                top: 0;
                bottom: 0;
                left: 0;
                background: rgba(0, 0, 0, 0.3);
                content: "";
                opacity: 0;
                -webkit-transition: all 0.3s linear;
                -moz-transition: all 0.3s linear;
                transition: all 0.3s linear;
                z-index: 4;
            }
            a:hover:after {
                opacity: 1;
            }
        }
    }
}
@media (min-width: 768px) {
    .project-name {
        font-size: 22px;
    }
    .image_box {
        width: 50vw;
    }
}
@media (max-width: 768px) {
    .image_box {
        width: 100vw;
    }
}
.port {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding-top: 100px;
    background: white;
    background-image: url("../images/26.jpg");
    background-position: top;
    background-repeat: no-repeat;
    z-index: 103;
    visibility: hidden;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    -webkit-transition: all 0.5s ease-in-out;
    -moz-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    img {
        width: 50%;
    }
    .description {
        float: left;
        width: 50%;
        max-height: 100%;
        padding: 0 40px 40px;
        overflow: auto;
    }
    h1 {
        font-size: 35px;
        line-height: 2.3;
        padding: 0;
    }
}
.port > * {
    opacity: 0;
    -webkit-transition: all 0.5s linear;
    -moz-transition: all 0.5s linear;
    transition: all 0.5s linear;
}
.port.item_open {
    visibility: visible;
    -webkit-transform: translateY(0%);
    transform: translateY(0%);
    -webkit-transition: all 0.4s ease-in-out;
    -moz-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
}
.port > * {
    opacity: 1;
    -webkit-transition-delay: 0.5s;
    transition-delay: 0.5s;
}
