    position: absolute;
    right: 50px;
    margin-top: 0;
    opacity: 1;
    z-index: 1004;
    -webkit-transition: all 0.1s ease-in-out;
    -moz-transition: all 0.1s ease-in-out;
    transition: all 0.1s ease-in-out;
}
.item_open {
    .close {
        opacity: 1;
        top: 40px;
        -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }
}
@media(max-width:325px) {
    .port {
        width: 100%;
        min-height: 800px;
        height: auto;
        img {
          width: 100%;
            height: 200px;
            position: relative;
            margin-top: -85px;
        }
        .description {
            width: 100%;
        }
    }
    .item_open {
        .close {
          top:740px;
          width: 31px;
          height: 31px;
          right: 42%;
        }
    }
    .galeria-box{
      left: -32px;
    }
}
